.song__container {
	position: relative;
	min-width: 240px;
	height: 240px;
	margin: 24px 18px;
}
.song__container:hover::before {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.8);
}
.song__container img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.song__container .song__info {
	display: none;
}

.song__container:hover .song__info {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.song__buttonPrimary {
	cursor: pointer;
	font-size: 64px !important;
}

.song__buttonSecondary {
	font-size: 34px !important;
}
